export var addToReadList = ()=>{

  r_list = document.getElementById('modal-content').childNodes;
  let r_list_lenght = document.getElementById('modal-content').childNodes.length

  for(var i=5;i<r_list_lenght;i+=8){

    r_list[i].addEventListener('click',addToReadListClick);
    //r_list[i+1].addEventListener('click',removeMyBookClick);
    r_list[i].value=i;
    //r_list_buttons[i]=1;
    console.log(r_list_buttons[i]);

  }

}


