


import {removeRead} from './removeFromReadList.js';
import {readBook} from './readFromReadList.js'


export var displayMyCart = ()=>{

  let cart = JSON.parse(readListJson);
//  para.innerHTML = cart[0].book;
//  console.log(cart[0].book);

  document.getElementById('mySidenav').innerHTML="";
  var heading = document.createElement("h2");
  heading.innerHTML="My Reading List";
  heading.style="text-align:center"

  document.getElementById("mySidenav").appendChild(heading);

  for(var i=0;i<cart.length;i++){

    var book = document.createElement("img");
    book.src=cart[i].imgsrc;
    document.getElementById("mySidenav").appendChild(book);

    var br = document.createElement("br");
    document.getElementById("mySidenav").appendChild(br);

    var remove = document.createElement("button");
    remove.innerHTML="Remove";
    remove.style.setProperty("background-color","red");
    document.getElementById("mySidenav").appendChild(remove);

    var read = document.createElement("button");
    read.innerHTML="Read";
    read.style.setProperty("background-color","#2a52be");
    document.getElementById("mySidenav").appendChild(read);

    var br = document.createElement("br");
    document.getElementById("mySidenav").appendChild(br);
    var br2 = document.createElement("br");
    document.getElementById("mySidenav").appendChild(br2);

    r_list_book_names.push(cart[i].book);



  }

  removeRead();
  readBook();




}
