

export var readBook = ()=>{

  var read_nodes = document.getElementById('mySidenav').childNodes;
  var read_nodes_length =  document.getElementById('mySidenav').childNodes.length;

  for(var k =4;k<read_nodes_length;k+=6){

    read_nodes[k].addEventListener('click',readBookClick);
    read_nodes[k].value = k;
    //rm_buttons[k]=1;

}

}

function readBookClick(){
 let bname = readListArr[((this.value-4)/6)].book;
 let preview_link = previewLinks[bname];
console.log(readListArr[((this.value-4)/6)].book);
window.open(preview_link, '_blank').focus();



}
